/* prefix ".codeblock-theme-coy" added by @codeblock/themes */
/**
 * prism.js Coy theme for JavaScript, CoffeeScript, CSS and HTML
 * Based on https://github.com/tshedor/workshop-wp-theme (Example: http://workshop.kansan.com/category/sessions/basics or http://workshop.timshedor.com/category/sessions/basics);
 * @author Tim  Shedor
 */
.codeblock-theme-coy code[class*="language-"],.codeblock-theme-coy 
pre[class*="language-"] {
    color: black;
    background: none;
    font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
    font-size: 1em;
    text-align: left;
    white-space: pre;
    word-spacing: normal;
    word-break: normal;
    word-wrap: normal;
    line-height: 1.5;
    -moz-tab-size: 4;
    -o-tab-size: 4;
    tab-size: 4;
    -webkit-hyphens: none;
    -ms-hyphens: none;
    hyphens: none;
}

/* Code blocks */
.codeblock-theme-coy pre[class*="language-"] {
    position: relative;
    margin: .5em 0;
    overflow: visible;
    padding: 0;
}

.codeblock-theme-coy pre[class*="language-"]>code {
    position: relative;
    border-left: 10px solid #358ccb;
    box-shadow: -1px 0px 0px 0px #358ccb, 0px 0px 0px 1px #dfdfdf;
    background-color: #fdfdfd;
    background-image: linear-gradient(transparent 50%, rgba(69, 142, 209, 0.04) 50%);
    background-size: 3em 3em;
    background-origin: content-box;
    background-attachment: local;
}

.codeblock-theme-coy code[class*="language"] {
    max-height: inherit;
    height: inherit;
    padding: 0 1em;
    display: block;
    overflow: auto;
}

/* Margin bottom to accommodate shadow */
.codeblock-theme-coy :not(pre) > code[class*="language-"],.codeblock-theme-coy 
pre[class*="language-"] {
    background-color: #fdfdfd;
    box-sizing: border-box;
    margin-bottom: 1em;
}

/* Inline code */
.codeblock-theme-coy :not(pre) > code[class*="language-"] {
    position: relative;
    padding: .2em;
    border-radius: 0.3em;
    color: #c92c2c;
    border: 1px solid rgba(0, 0, 0, 0.1);
    display: inline;
    white-space: normal;
}

.codeblock-theme-coy pre[class*="language-"]:before,.codeblock-theme-coy 
pre[class*="language-"]:after {
    content: '';
    z-index: -2;
    display: block;
    position: absolute;
    bottom: 0.75em;
    left: 0.18em;
    width: 40%;
    height: 20%;
    max-height: 13em;
    box-shadow: 0px 13px 8px #979797;
    transform: rotate(-2deg);
}

.codeblock-theme-coy :not(pre) > code[class*="language-"]:after,.codeblock-theme-coy 
pre[class*="language-"]:after {
    right: 0.75em;
    left: auto;
    transform: rotate(2deg);
}

.codeblock-theme-coy .token.comment,.codeblock-theme-coy 
.token.block-comment,.codeblock-theme-coy 
.token.prolog,.codeblock-theme-coy 
.token.doctype,.codeblock-theme-coy 
.token.cdata {
    color: #7D8B99;
}

.codeblock-theme-coy .token.punctuation {
    color: #5F6364;
}

.codeblock-theme-coy .token.property,.codeblock-theme-coy 
.token.tag,.codeblock-theme-coy 
.token.boolean,.codeblock-theme-coy 
.token.number,.codeblock-theme-coy 
.token.function-name,.codeblock-theme-coy 
.token.constant,.codeblock-theme-coy 
.token.symbol,.codeblock-theme-coy 
.token.deleted {
    color: #c92c2c;
}

.codeblock-theme-coy .token.selector,.codeblock-theme-coy 
.token.attr-name,.codeblock-theme-coy 
.token.string,.codeblock-theme-coy 
.token.char,.codeblock-theme-coy 
.token.function,.codeblock-theme-coy 
.token.builtin,.codeblock-theme-coy 
.token.inserted {
    color: #2f9c0a;
}

.codeblock-theme-coy .token.operator,.codeblock-theme-coy 
.token.entity,.codeblock-theme-coy 
.token.url,.codeblock-theme-coy 
.token.variable {
    color: #a67f59;
    background: rgba(255, 255, 255, 0.5);
}

.codeblock-theme-coy .token.atrule,.codeblock-theme-coy 
.token.attr-value,.codeblock-theme-coy 
.token.keyword,.codeblock-theme-coy 
.token.class-name {
    color: #1990b8;
}

.codeblock-theme-coy .token.regex,.codeblock-theme-coy 
.token.important {
    color: #e90;
}

.codeblock-theme-coy .language-css .token.string,.codeblock-theme-coy 
.style .token.string {
    color: #a67f59;
    background: rgba(255, 255, 255, 0.5);
}

.codeblock-theme-coy .token.important {
    font-weight: normal;
}

.codeblock-theme-coy .token.bold {
    font-weight: bold;
}

.codeblock-theme-coy .token.italic {
    font-style: italic;
}

.codeblock-theme-coy .token.entity {
    cursor: help;
}

.codeblock-theme-coy .token.namespace {
    opacity: .7;
}

@media screen and (max-width: 767px){
    .codeblock-theme-coy pre[class*="language-"]:before,.codeblock-theme-coy 
	pre[class*="language-"]:after {
        bottom: 14px;
        box-shadow: none;
    }

}

/* Plugin styles */
.codeblock-theme-coy .token.tab:not(:empty):before,.codeblock-theme-coy 
.token.cr:before,.codeblock-theme-coy 
.token.lf:before {
    color: #e0d7d1;
}

/* Plugin styles: Line Numbers */
.codeblock-theme-coy pre[class*="language-"].line-numbers.line-numbers {
    padding-left: 0;
}

.codeblock-theme-coy pre[class*="language-"].line-numbers.line-numbers code {
    padding-left: 3.8em;
}

.codeblock-theme-coy pre[class*="language-"].line-numbers.line-numbers .line-numbers-rows {
    left: 0;
}

/* Plugin styles: Line Highlight */
.codeblock-theme-coy pre[class*="language-"][data-line] {
    padding-top: 0;
    padding-bottom: 0;
    padding-left: 0;
}

.codeblock-theme-coy pre[data-line] code {
    position: relative;
    padding-left: 4em;
}

.codeblock-theme-coy pre .line-highlight {
    margin-top: 0;
}


